<template>
    <div class="download-page">
        <!-- <div class="download-top">
            <img src="../assets/imgs/download-top.png" alt="">
        </div> -->
        <div class="download-content">
            <div class="download-box">
                <div class="download-img">
                    <img :src="download_ewm_img" alt="">
                </div>

                <div class="download-btn">
                    <img class="download-btn-left" src="../assets/imgs/iphone-btn.png" alt="" @click="download1('ios')">
                    <img class="download-btn-right" src="../assets/imgs/android-btn.png" alt="" @click="download1('android')">
                    <!-- <div class="download-btn-left" @click="download1('ios')">iOS下载</div>
                    <div class="download-btn-right" @click="download1('android')">Android下载</div> -->
                </div>
            </div>
        </div>

        
    </div>
</template>


<script>
export default {
    data(){
        return{
            download_ewm_img: this.http.download_ewm_img,
            download_android_url:this.http.download_android_url,
            download_ios_url:this.http.download_ios_url,
        }
    },
    mounted(){
        this.utils.info('download_ewm_img...',this.download_ewm_img);
        this.utils.info('download_android_url...',this.download_android_url);
        this.utils.info('download_ios_url...',this.download_ios_url);
    },
    methods:{
        //判断是否是微信浏览器的函数
        isWeiXin(){
            //window.navigator.userAgent属性包含了浏览器类型、版本、操作系统类型、浏览器引擎类型等信息，这个属性可以用来判断浏览器类型
            var ua = window.navigator.userAgent.toLowerCase();
            //通过正则表达式匹配ua中是否含有MicroMessenger字符串
            if(ua.match(/MicroMessenger/i) == 'micromessenger'){
                return true;
            }else{
                return false;
            }
        },
        isIos(){
            var u = navigator.userAgent;
            if(!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)){
                document.getElementById('downts').innerHTML = '点击右上角按钮，然后在弹出菜单中，点击Safari中打开，即可安装';
                document.getElementById('bb').innerHTML = '版本：V1.0.7（builld 7）';
                document.getElementById('dx').innerHTML = '大小：52M';
                return true;
            }else{
                return false;
            }
        },
        download1(val){
            this.utils.info('var...',val);
            this.utils.info('isWeiXin...',this.isWeiXin());
            if(this.isWeiXin()){
                // 是微信浏览器，执行操作
                this.utils.showAlert('点击右上角按钮，然后在弹出菜单中，点击浏览器中打开，即可安装');
            }else if(val == 'ios'){
               
                // window.location.href="itms-services://?action=download-manifest&url=https://download.rmq168.com/software/manifest.plist";
                //window.location.href="itms-services://?action=download-manifest&url=http://dy.rmq168.com/web/download/Info.plist";
                window.location.href = this.download_ios_url;
            }else{
                
                window.location.href = this.download_android_url;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    .download-page{
        width:100%;
        height:100%;
        background: url(../assets/imgs/download.png) no-repeat center top;
