        bottom: 0;
        padding:0 15px 40px;
    }

    .download-box{
        // width:300px;
        // background: #ff8379;
        // border-radius: 10px;
        // margin:0 auto;
        // padding:30px 15px;
        background: linear-gradient(to bottom, #559FFC,#8FCEFF);
        padding:40px 15px 20px;
    }

    .download-img img{
        display: block;
        width:150px;
        margin:0 auto;
    }

    .download-btn{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top:70px;
    }
    .download-btn-left{
        // background: #F85553;
